const useHandbookInterestRate = (arr = []) => {
    const dispatch = useDispatch();

    const columns = [
        { field: 'id', headerName: 'Stock', width: 200, hideable: false },
        {
            field: 'price',
            headerName: 'Price',
            ...usdPrice
        },
        { field: 'stock_of_capital', headerName: 'Stock of capital', ...usdPrice },
        {
            field: 'number_of_persons_employed',
            headerName: 'Number of persons employed',
            type: 'number',
            minWidth: 250,
            cellClassName: moreThanZero
        },
        { field: 'total_money_supply', headerName: 'Total money supply', ...usdPrice },
        {
            field: 'interest_on_capital',
            headerName: 'Interest on capital',
            type: 'number',
            minWidth: 200,
            cellClassName: moreThanZero,
            valueFormatter: (params) => {
                if (params.value) {
                    const valueFormatted = Number(params.value).toLocaleString();
                    return `${valueFormatted} %`;
                }
                return '0 %';
            }
        },
        { field: 'market_cap', headerName: 'Market Cap', ...usdPrice }
    ];
    const [rowsState, setRowsState] = useState(rows);
    const [calcExpression, setCalcExpression] = useState([]);
    const [checkCalcExpression, setCheckCalcExpression] = useState([]);
    const [checkedColumn, setCheckedColumn] = useState('price');
    const [columnsState, setColumnsState] = useState(columns);
    const [uniqueVariableId, setUniqueVariableId] = useState('');
    const [uniqueVariableName, setUniqueVariableName] = useState('');
    const [selectedCheckBox, setSelectedCheckBox] = useState([]);
    const [list, setList] = useState([]);
    const [isOpenCreateModal, setIsOpenCreateModal] = useState(false);
    const [issueRate, setIssueRate] = useState(0);
    const [daysInMonth, setDaysInMonth] = useState('DaysFact');
    const [reloadPage, setReloadPage] = useState(false);
    const [rateSettingId, setRateSettingId] = useState(2);
    const [additionColumn, setAdditionColumn] = useState(0);

    useEffect(() => {
        if (additionColumn <= +localStorage.getItem('counter_f_i_e_l_d')) {
            const columnsFromLocal = Object.entries(localStorage);

            const newColumnsFromLocal = columnsFromLocal.filter((arr) => arr[0].slice(0, 10) === 'f_i_e_l_d_');

            if (newColumnsFromLocal.length === 0) return null;
            newColumnsFromLocal.sort((a, b) => {
                const indexA = a[0].indexOf('*');
                const numberA = +a[0].slice(10, indexA);
                const indexB = b[0].indexOf('*');
                const numberB = +b[0].slice(10, indexB);

                return numberA - numberB;
            });

            try {
                const index = newColumnsFromLocal[additionColumn][0].indexOf('*');
                const key = newColumnsFromLocal[additionColumn][0].slice(index + 1);
                const obj = JSON.parse(localStorage.getItem(newColumnsFromLocal[additionColumn][0]));
                const addedObject = {
                    field: key,
                    headerName: obj.headerName,
                    type: 'number',
                    width: 200,
                    cellClassName: moreThanZero,
                    valueGetter: (params) => {
                        let st = '';
                        obj.formul.forEach((value) => {
                            if (typeof params.row[value] === 'number') {
                                st += params.row[value];
                            } else if (['pow(', 'exp(', 'log(', 'sqrt(', 'cos(', 'sin(', 'tan('].includes(value)) {
                                st += `Math.${value}`;
                            } else {
                                st += value;
                            }
                        });
                        params.row[key] = Number((window.eval(st) && window.eval(st).toFixed(2)) || 0);
                        return Number((window.eval(st) && window.eval(st).toFixed(2)) || 0);
                    }
                };
                setColumnsState([...columnsState, addedObject]);
                setAdditionColumn(additionColumn + 1);
            } catch {
                setAdditionColumn(additionColumn + 1);
            }
        }
        return null;
    }, [additionColumn]);

    const commonHandlers = {
        hanleCheckedColumn: (e) => setCheckedColumn(e.target.value),
        handleRateSettingId: (e) => setRateSettingId(e.target.value),
        handleSelectTableRow: (event, id = null, data = null) => setSelectedCheckBox([id]),
        handleUniqueVariableId: (e) => setUniqueVariableId(e.target.value),
        handleUniqueVariableName: (e) => setUniqueVariableName(e.target.value),
        handleCalcExpression: (realArr, checkArr) => {
            setCalcExpression(realArr);
            setCheckCalcExpression(checkArr);
        },
        handleIsOpenCreateModal: () => {
            setIsOpenCreateModal(!isOpenCreateModal);
            setUniqueVariableId('');
            setUniqueVariableName('');
            setCalcExpression([]);
            setCheckCalcExpression([]);
        },
        handleDaysInMonth: (e) => setDaysInMonth(e.target.value)
    };

    return [
        {
            additionColumn,
            calcExpression,
            checkCalcExpression,
            uniqueVariableId,
            uniqueVariableName,
            checkedColumn,
            rowsState,
            columnsState,
            rateSettingId,
            daysInMonth,
            issueRate,
            isOpenCreateModal,
            selectedCheckBox,
            list
        },
        { ...commonHandlers },
        {}
    ];
};

