import React from 'react';

import { useDispatch } from 'react-redux';
import { Button, Box } from '@mui/material';
import { useHandbookInterestRate } from '@/core/hooks';

import { Table, ButtonsBoxRow, Loader } from '@/components';
import { ModalHandbookInterestRate, Alert } from '@/containers';

import CreateNewFolderIcon from '@mui/icons-material/CreateNewFolder';

const BondIssueList = () => {
    const [commonStates, commonHandlers, commonErrors] = useHandbookInterestRate();

    return (
        <>
            <Alert />
            <Box gridColumn="span 12" sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <ButtonsBoxRow>
                    <div>
                        Today : {new Date().getFullYear()}-{Number(new Date().getMonth()) + 1}-{new Date().getDate()}{' '}
                    </div>
                </ButtonsBoxRow>

                <Button
                    variant="contained"
                    startIcon={<CreateNewFolderIcon />}
                    sx={{ background: '#0064ff' }}
                    onClick={commonHandlers.handleIsOpenCreateModal}
                >
                    Create
                </Button>
            </Box>
            <Box gridColumn="span 12">
                <Table commonStates={commonStates} />
            </Box>
            <ModalHandbookInterestRate
                commonStates={commonStates}
                commonHandlers={commonHandlers}
                title="Сreate a new variable and add it to a table column"
            />
        </>
    );
};

export default BondIssueList;
